@import url(https://fonts.googleapis.com/css?family=Open+Sans:regular,500,600,700,800&display=swap);
*{padding:0px;margin:0px;border:0px}*,*:before,*:after{box-sizing:border-box}aside,nav,footer,header,section{display:block}html,body{height:100%;min-width:320px}body{line-height:1.5;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-size:inherit}input::-ms-clear{display:none}button{cursor:pointer;background-color:inherit;border:none;background:none;display:block}button,button:hover,button:focus{outline:none;border:none;cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none;display:block}a:hover{text-decoration:none}ul li{list-style:none;display:block;padding:0;margin:0}img{vertical-align:top}img{display:block;max-width:100%}h1,h2,h3,h4,h5,h6{padding:0;margin:0}
body{font-family:"Open Sans",sans-serif;font-size:20px;background:#000 url(/Films/static/media/back_img_offer.dde5d613.png);margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body a{color:#000}code{font-family:"OpenSans",monospace}.page{flex:1 1 auto}.logo{width:100px;height:100px}.wrapper{width:100%;min-height:100%;display:flex;flex-direction:column;height:100vh}.wrapper main{flex:1 0 auto}.wrapper footer{flex:0 0 auto}
